  font-family: "Raleway", sans-serif;
  font-optical-sizing: auto;
  font-weight: 200;
  font-style: normal;
  height: 100vh;
  width: 100%;
  margin: 0;
}

nav {
  font-family: "Raleway", sans-serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: normal;
  background-color: #92e0e5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 75px;
}

.links {
  color: #f3fcfd;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 200px;
  list-style: none;
  margin-right: 30px;
}

.dog-icon {
  width: 50px;
  height: 50px;
  margin-left: 40px;
}

