  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) center/cover;
  background-size: 120%;
  background-position: center 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 600px;
}

h1 {
  color: #3d3a39;
  font-weight: 500;
  margin-bottom: 25px;
}

h2 {
  font-weight: 400;
  font-size: small;
}

.main-dog-pic {
  border: 2px solid #3f8da7;
  border-radius: 50%;
  height: 350px;
  width: 350px;
  margin-bottom: 20px;
}

.button {
  font-family: "Raleway", sans-serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: normal;
  background-color: #ffffff;
  border: 5px solid #3f8da7;
  color: #3f8da7;
  margin-top: 20px;
  width: 170px;
  height: 45px;
}

.dog-content {
  display: flex;
  justify-content: baseline;
  align-items: center;
  height: 650px;
}

.dog-cards {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  margin-top: 25px;
  padding-left: 35px;
  padding-right: 35px;
}

.secondary-dog-pics {
  border: 2px solid #3f8da7;
  border-radius: 50%;
  height: 250px;
  width: 250px;
  margin-bottom: 15px;
  margin-top: 15px;
}

p {
  font-family: "Raleway", sans-serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: normal;
  font-size: medium;
  text-align: center;
}

.social-media-links {
  background-color: #3f8da8;
  color: #f3fcfd;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0;
}

.social-quicklinks {
  width: 600px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 25px;
}

a {
  color: #f3fcfd;
  font-weight: 500;
